import { Image } from 'antd';
import ReactMarkdown from 'react-markdown';

import './House.scss';

const buildCitation = props => 
  <span>
    {`${props.user_created.last_name}, ${props.user_created.first_name.substring(0, 1)}., "${props.house_name}", Tetrastylon, ${new Date(props.date_updated).getUTCFullYear()}`}
    , <a href={window.location}>{window.location.href}</a>.
  </span>

const HouseLoaded = props => {

  const imageIds = props.images.map(img => img.directus_files_id);

  const backgroundImage = imageIds.length > 0 ? `url('${process.env.REACT_APP_API_URL}/assets/${imageIds[0]}?width=1400&height=600')` : null; 

  return (
    <div className="house loaded">
      <header className="jumbo" style={{ backgroundImage }}>
        <div className="jumbo-contents">
          <h1>{ props.house_name }</h1>          
          <h2>{ props.city }</h2>
          <span className="house-properties">{ props.number_of_rooms } Rooms, { parseFloat(props.house_dimensions).toFixed() } mÂ²</span>
        </div>
      </header>

      <main>
        <div className="section">
          <h3>Description</h3>
          <ReactMarkdown>{ props.house_description }</ReactMarkdown>
        </div>

        <div className="section">
          <h3>Dating of Phases</h3>
          <ReactMarkdown>{ props.dating_of_phases }</ReactMarkdown>
        </div>

        <div className="section">
          <h3>Representation Room Typology</h3>
          <ReactMarkdown>{ props.representation_room_typology }</ReactMarkdown>
        </div>

        <div className="section">
          <h3>Situation of Entrance</h3>
          <ReactMarkdown>{ props.situation_of_entrance }</ReactMarkdown>
        </div>

        <div className="section">
          <h3>Other Uncovered Spaces</h3>
          <ReactMarkdown>{ props.other_uncovered_spaces }</ReactMarkdown>
        </div>

        { props.images.length > 0 &&
          <div className="section images">
            <h3>Images</h3>
            <Image.PreviewGroup>
              { props.images.map(img => 
                <Image 
                  key={img.id} 
                  width={200} 
                  src={`${process.env.REACT_APP_API_URL}/assets/${img.directus_files_id}?width=200&height=200`}
                  preview={{ src:`${process.env.REACT_APP_API_URL}/assets/${img.directus_files_id}?width=1200` }} />
              )}
            </Image.PreviewGroup>
          </div>
        }

        { props.plans_of_the_house.length > 0 &&
          <div className="section plans">
            <h3>Plans of the House</h3>
            <Image.PreviewGroup>
              { props.plans_of_the_house.map(img => 
                <Image 
                  key={img.id} 
                  width={200} 
                  src={`${process.env.REACT_APP_API_URL}/assets/${img.directus_files_id}?width=200&height=200`}
                  preview={{ src:`${process.env.REACT_APP_API_URL}/assets/${img.directus_files_id}?width=1200` }} />
              )}
            </Image.PreviewGroup>
          </div>
        }

        <div className="section cite-as">
          <h3>Cite as</h3>
          <p>
            { buildCitation(props) }
          </p>
        </div>
      </main>
    </div>
  )

}

export default HouseLoaded;